import React from 'react'
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";

function NavBar() {
    return (
        <Router>
            <div>
                <ul>
                    <li>
                        <Link to="/">Main</Link>
                    </li>
                    <li>
                        <Link to="/project">Project</Link>
                    </li>
                </ul>
            </div>
            <hr />
        </Router>
    )
}

export default NavBar
